<template>
  <div class="login d-flex align-center tableImage">
    <!-- 訊息顯示 -->
    <v-snackbar
      top
      color="warning"
      :value="this.$store.state.snackbar"
      @input="close"
    >
      <span>{{ this.$store.state.snackbarMsg }}</span>
      <v-btn text @click.native="close(false)">Close</v-btn>
    </v-snackbar>

    <v-row class="justify-center">
      <v-col cols="8" class="myTransparent pa-6" style="border-radius:20px">
          <div class="titleStyle d-flex justify-space-between">
            <h1>長照護理系統</h1>
            <h3 class="align-self-end"> {{ $store.state.verMsg }} </h3>
          </div>
          <h1 class="red--text">{{ overtimeMsg }}</h1>
          <hr />

          <v-form class="mt-3 pa-3 setBgColor-1" ref="form" style="border-radius:0px 0px 10px 10px">
            <h2 class="px-2">
              {{ $store.state.cData.company_name }}
            </h2>
            <v-text-field
              label="帳號 (Account/E-mail)"
              prepend-icon="mdi-codepen"
              autofocus
              required
              v-model="list.email"
              ref="email"
              :rules="emptyRules"
              @keyup.enter="submit"
            ></v-text-field>
            <v-text-field
              type="password"
              label="密碼 (Password)"
              prepend-icon="mdi-onepassword"
              required
              v-model="list.password"
              :rules="emptyRules"
              @keyup.enter="submit"
            ></v-text-field>

            <v-btn block :color="$store.state.colorSet.bgColor4" class="mt-2 pa-2 white--text font-weight-bold" @click.stop="submit"
              >登入</v-btn
            >

            <div class="d-flex justify-end mt-2">
              <span>{{ this.$store.state.cData.last_login_time }}</span>
              <v-spacer></v-spacer>
              <v-icon>mdi-registered-trademark</v-icon>
              <span>2020.1.2</span>
            </div>
            <!-- <pre>
              {{this.$store.state.cData}}
            </pre> -->
          </v-form>

        


        <!-- 搖動動畫 -->
        <!-- <button @mouseenter="noActivated = true">Shack It!</button>
        <div class="block" :class="{ shake: noActivated }" @animationend="reactivated">Block</div> -->
        
      </v-col>

    </v-row>

  </div>
</template>

<script>
// import medicineItems from '@/assets/json/myMedicine.json'

export default {
  data() {
    return {
      list: {}, //表單資料
      emptyRules: [(v) => !!v || "不可空白"],
      overtimeMsg: "",
      noActivated: false,
      // medicineItems:""

      // isNetLinked: true,  //判斷網路連線是否正常
    };
  },

  mounted() {
    this.getData();

    //2023.7.29 判斷網路連線是否正常
    // if (navigator.onLine) {
    //   console.log("login,網路連線正常");
    //   this.isNetLinked = true;
    // } else {
    //   this.isNetLinked = false;
    //   console.log("login,網路連線中斷");
    // }
    // this.medicineItems = medicineItems
    //處理官方藥名to JSON檔
    // let medicineName = medicineItems
    // .map(i=>{
    //   return {
    //      "en-name": i.英文品名,
    //      "ch-name": i.中文品名,
    //      "ch-name": i.中文品名,
    //      "Indications": i.適應症,
    //   }
    // });
    // // this.medicineItems = JSON.stringify(medicineName)
    // console.log(JSON.stringify(medicineName))
  },


  methods: {
    reactivated() {
      this.noActivated = false;
    },
    close(val) {
      var pop = { msg: "", type: val };
      this.$store.commit("snackbar", pop);
    },
    getData() {
      //取得廠商資料
      this.$api.options("login/getCData/" + this.$store.state.databaseName)
        .then((rs) => {
          // console.log(rs)
          this.$store.commit("setcData", rs.cData);
          sessionStorage.setItem("cData", JSON.stringify(rs.cData));
        })
        .catch((error) => {
          // console.log('login error:',error)
          if (error == 'network error'){
            this.overtimeMsg = '網路連線環境異常,請確認本地網路連線狀態!!';  
          }
          if (error == 'server not working'){
            this.overtimeMsg = "系統資料庫連結失敗,請洽詢系統工程師";
          }
          // if (this.isNetLinked){
          //   this.overtimeMsg = "系統資料庫連結失敗,請洽詢系統工程師";
          // }else{
          //   this.overtimeMsg = '網路連線環境異常,請確認本地網路連線狀態!!';
          // }
        });
    },
    submit() {
      this.$api.options("login/logining_nosql/" + this.$store.state.databaseName,this.list)
      .then((rs) => {
        if (rs.state == "logined") {
          //判斷停權
          if (rs.pData.suspension == "true" || rs.pData.suspension) {
            var pop = { msg: "未授權的使用者!請重新確認!!", type: true };
            this.$store.commit("snackbar", pop);
          } else {
            this.$store.commit("setpData", rs.pData);
            sessionStorage.setItem("pData", JSON.stringify(rs.pData));
            this.$store.commit("logined", true);
            this.$router.push("/Main/functionlist");
          }
        } else {
          var pop = { msg: "登入資料有誤!請重新確認", type: true };
          this.$store.commit("snackbar", pop);
          this.$refs.email.focus();
        }
      });

    },
  },
};
</script>

